@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
.ErrorMessage {
    width  : 100%;
    height : 100vh;
    display: flex;
}

.ErrorMessageContent {
    max-width       : 600px;
    padding         : 30px;
    background-color: lightcoral;
    color           : #f2f2f2;
    margin          : auto;
    border-radius   : 8px;
}

.Preloader {
    width  : 100%;
    height : 100vh;
    display: flex;
}

.PreloaderSpinner {
    position: relative;
    width   : 80px;
    height  : 80px;
    margin  : auto;
}

.PreloaderSpinner::before {
    display      : block;
    content      : "";
    position     : absolute;
    top          : 0;
    left         : 0;
    width        : 100%;
    height       : 100%;
    border       : 3px solid rgba(33, 150, 243, 0.3);
    border-radius: 50%;
}

.PreloaderSpinner::after {
    display         : block;
    content         : "";
    width           : 100%;
    height          : 100%;
    position        : absolute;
    top             : 0;
    left            : 0;
    border          : 3px solid transparent;
    border-top-color: rgba(33, 150, 243, 1);
    border-radius   : 50%;
    animation       : preloader-spinner 1s linear 0s infinite;
}

@keyframes preloader-spinner {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.FilterPanelLabel {
    display    : flex;
    align-items: center;
    padding    : 10px 20px;

    cursor: pointer;

    font-size  : 13px;
    line-height: 20px;
    color      : #4A4A4A;
}

.FilterPanelLabel:hover {
    background-color: #F1FCFF;
}

.FilterPanelLabel input {
    width       : 20px;
    height      : 20px;
    margin-right: 10px;
}

.FilterPanel {
    background   : #FFFFFF;
    box-shadow   : 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding      : 20px 0;
}

.FilterPanelTitle {
    font-weight   : 600;
    font-size     : 12px;
    line-height   : 12px;
    text-transform: uppercase;
    color         : #4A4A4A;
    margin-bottom : 20px;
    padding       : 0 20px;
}

.ButtonSort {
    width  : 50%;
    padding: 15px;

    border            : 1px solid #DFE5EC;
    border-left-width : 0;
    border-right-width: 0;
    background-color  : #fff;
    outline           : none;
    cursor            : pointer;

    font-family   : inherit;
    font-weight   : 600;
    font-size     : 12px;
    letter-spacing: 0.5px;
    line-height   : 20px;
    color         : #4A4A4A;
    text-transform: uppercase;

    transition: background-color 0.3s, border-color 0.3s;
}

.ButtonSort:first-child {
    border-top-left-radius   : 5px;
    border-bottom-left-radius: 5px;
    border-left-width        : 1px;
}

.ButtonSort:last-child {
    border-top-right-radius   : 5px;
    border-bottom-right-radius: 5px;
    border-right-width        : 1px;
}

.ButtonSort.active {
    background-color: #2196F3;
    color           : #fff;
    border-color    : #2196F3;
}

@media(max-width: 560px) {
    .ButtonSort {
        padding: 12px;

        font-size: 10px;
    }
}

.SortPanel {
    display      : flex;
    color        : blue;
    margin-bottom: 20px;
}

.TicketInfoHeader {
    font-style    : normal;
    font-weight   : 600;
    font-size     : 12px;
    line-height   : 18px;
    color         : #A0B0B9;
    text-transform: uppercase;
}

.TicketInfoContent {
    font-style : normal;
    font-weight: 600;
    font-size  : 14px;
    line-height: 21px;
    color      : #4A4A4A;
}

.Ticket {
    display              : grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap             : 20px;
    margin-bottom        : 20px;
    padding              : 20px;

    background-color: #fff;
    box-shadow      : 0 2px 8px rgba(0, 0, 0, 0.1);
    border-radius   : 5px;
}

.TicketPrice {
    grid-column-start: 1;
    grid-column-end  : 3;
    align-self       : center;

    font-weight: 600;
    font-size  : 24px;
    line-height: 24px;
    color      : #2196F3;
}

.TicketLogo {
    width: 110px;
}

@media(max-width: 560px) {
    .Ticket {
        grid-template-columns: 1fr 1fr;
    }

    .TicketPrice {
        grid-column-start: 1;
        grid-column-end  : 2;
        font-size        : 20px;
    }
}

.TicketList {
    width: 100%;
}

.ButtonLoadMore {
    display: block;
    width  : 200px;
    padding: 15px;
    margin : 0 auto;

    background-color: #2196F3;
    border          : none;
    outline         : none;
    border-radius   : 5px;
    cursor          : pointer;

    font-size: 16px;
    color    : #fff;

    transition: box-shadow 0.3s;
}

.ButtonLoadMore:hover {
    box-shadow: 0 0 15px #2196F3;
}

.Logo {
    display        : flex;
    justify-content: center;
    margin         : 50px auto;

    width        : 60px;
    height       : 60px;
    border-radius: 50%;

    box-shadow: 0px 8px 11px rgba(55, 104, 142, 0.25);
}

.LogoImage {
    width : 100%;
    height: 100%;

    object-fit     : cover;
    object-position: center;

    transform: scale(1.06);
}

@media(max-width: 560px) {
    .Logo {
        margin: 20px auto;
    }
}

*,
::before,
::after {
    box-sizing: border-box;
    margin    : 0;
    padding   : 0;
}

body {
    background-color: #f3f7fa;
    font-family     : 'Open Sans', sans-serif;
    color           : #4A4A4A;
    font-style      : normal;
    font-weight     : normal;
}

.App {
    max-width: 794px;
    margin   : 0 auto;
}

.AppContainer {
    display              : grid;
    grid-template-columns: 232px 502px;
    grid-gap             : 20px;
    align-items          : flex-start;
    padding              : 0 20px 30px;
}

@media(max-width: 800px) {
    .AppContainer {
        grid-template-columns: 1fr;
    }
}

